html, body {
    margin: 0;
    padding: 0;
    font: 10px "Century Gothic", Futura, sans-serif;
    font-family: "Clear Sans", "Helvetica Neue", Arial, sans-serif;
    margin: 20px;
    background: #faf8ef;
    color: #776e65
  }

  body {
    margin: 80px 0; }
  
  ol, ul {
    padding-left: 30px;
  }

  .gameContainer {
    position: absolute;
    width: 280px;
    height: 585px;
    top: 50%;
    left: 50%;
    margin-left: -140px;
    margin-top: -345px;
    -webkit-animation: fade-in 800ms ease 1200ms;
    -moz-animation: fade-in 800ms ease 1200ms;
    animation: fade-in 800ms ease 1200ms;
    -webkit-animation-fill-mode: both;
    -moz-animation-fill-mode: both;
    animation-fill-mode: both;
  }

  .game-intro {
    float: left;
    height: 32px;
    margin-bottom: 0; 
    font-size: 13px;
  }

  .restart-button {
    background: #8f7a66;
    border-radius: 3px;
    padding: 0 20px;
    text-decoration: none;
    color: #f9f6f2;
    height: 16px;
    font-weight: bold;
    font-size: 15px;
    line-height: 15px;
    display: block;
    text-align: center;
    float: right;
  }

  .buttonContainer:after {
    content: "";
    display: block;
    clear: both;
  }

  .text2048 {
    text-align: center;
    color: #776e65;
    font-size: 40px;
    font-weight: bold;
  }

  .howToPlay {
    margin-left: 15px;
    background-color: #faf8ef;
    border-radius: 5px;
    border: 3px solid #faf8ef;
    font-size: 10px;
  }

  .header {
    /* display: flex; */
    margin-left: 5px;
    text-align: center;
    width: 250px;
    border: 10px solid #faf8ef;
    border-radius: 20px;
    min-height: 55px;
    background: #d3c60f70;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .board {
    margin-top: 5px;
    width: 275px;
    height: 300px;
    background: #bbada0;
    border-radius: 10px;
  }
  
  .board-row {
    clear: both;
    content: "";
    display: table;
  }

  .square {
    background: rgba(238, 228, 218, 0.35);
    float: left;
    width: 60px;
    height: 60px;
    margin-left: 7px;
    margin-top: 7px;
    padding: 0;
    text-align: center;
    border-radius: 5px;
  }

  .active-square {
    float: left;
    font-size: 30px;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    width: 60px;
    height: 60px;
    margin-left: 7px;
    margin-top: 7px;
    text-align: center;
    border-radius: 5px;
  }

  .score-board {
    font-size: 20px;
    background: rgb(238, 217, 160);
    width: 261px;
    height: 25px;
    border-radius: 10px;
    text-align: center;
    border: 7px solid #bbada0;
  }
  